/* App */

.data-visualizer-app {
    height: 100%;
    background-color: #f4f6f8;
    overflow: hidden;
}
.data-visualizer-app * {
    outline: none;
}

body {
    font-family: 'Roboto', sans-serif;
}

/* Flex */

.flex-ct {
    display: flex;
}

.flex-dir-col {
    flex-direction: column;
}

.flex-grow-1 {
    flex-grow: 1;
}

.flex-basis-0 {
    flex-basis: 0%;
}

/* Headerbar */

.section-headerbar {
    height: 48px;
}

/* Toolbar */

.section-toolbar {
    height: 39px;
}

.toolbar-type {
    width: 260px;
    border-right: 1px solid #e0e2e4;
    border-bottom: 1px solid #e0e2e4;
}

.toolbar-menubar {
    background-color: #ddd;
}

/* Main */

.main-left {
    min-width: 260px;
    border-right: 1px solid #e0e2e4;
}

.main-right {
    flex: 1 1 0%;
    max-width: 380px;
    background-color: #f0f2f4;
    overflow-y: auto;
    overflow-x: hidden;
}

/* Main center */

.main-center-canvas {
    overflow: hidden;
}

/* DimensionPanel dimension item */
/*
This keeps the dimension item clone from causing 
a ripple effect in the Dimension list during drag
*/
.dimension-item-clone ~ li {
    transform: none !important;
}

li.dragging {
    background-color: #e8edf2;
    border-radius: 2px;
}
